.cert-item-wrapper {
  width: min(700px, 100vw);
  background-color: rgb(240, 237, 237);
  margin-inline: auto;
  margin-top: 1vh;
  padding-bottom: 1vh;
  border-radius: 10px;
  border: 1px solid grey;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 5px;
}

.cert-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.1rem;
  padding-inline: 1.5rem;
  margin-bottom: 1vh;
  margin-inline: 2vw;
  background-color: #7f9eb2;
  border: 1px solid black;
  border-radius: 5px;
}

.cert-item h2 {
  width: 40%;
  font-weight: normal;
  font-size: 0.8rem;
  font-style: italic;
  margin-block-start: 0.3em;
  margin-block-end: 0.3em;
  border: 1px solid black;
  padding: 0.1rem;
  padding-inline: 1vw;
  background-color: white;
  border-radius: 5px;
}

.cert-item-top-row {
  display: flex;
  justify-content: space-between;
}

.Cert-item__subjectName {
  width: 80%;
  background-color: transparent;
  color: black;
  padding-top: 1vh;
  margin-left: 2rem;
  margin-bottom: 1vh;
  font-size: 1.2rem;
}

.deleteCert {
  font-size: 1rem;
  font-weight: normal;
  /* border: 1px solid black; */
  border-radius: 50%;
  margin-top: 1vh;
  height: 1.5vw;
  width: 1.5vw;
  margin-right: 1.2vw;
  background-color: #7f9eb2;
  color: rgb(61, 59, 59);
  text-align: center;
}

.deleteCert:hover {
  cursor: pointer;
  background-color: #a3b1ba;
  color: rgb(141, 31, 9);
  
}